<template>
  <span
    class="badge"
    :style="{ color: color, borderColor: color, fontSize: fontSize }"
    >{{ text }}</span
  >
</template>

<script>
export default {
  name: "Badge",
  props: {
    color: {
      default: "#d33d3e",
      validator(value) {
        //这段代码是定义组件的props属性。props属性中定义了一个color属性，这个属性的默认值为 #d33d3e，
        //并且还设置了一个validator函数，用来验证传入的值是否符合要求，这里的要求是传入的值必须以#开头，长度可以是4位或者7位。
        return (
          value.indexOf("#") === 0 && (value.length === 4 || value.length === 7)
        );
      },
    },
    text: {
      default: "",
    },
    fontSize: {
      default: "12px",
    },
  },
};
</script>

<style scoped>
.badge {
  padding-left: 3px;
  padding-right: 3px;
  border-width: 1px;
  border-style: solid;
  border-radius: 5px;
}
</style>
