<template>
  <div class="container">
    <!-- 搜索框 -->
    <div class="header">
      <div class="search-box" @click="toSearch">
        <i class="fa-search"></i>&nbsp;&nbsp;
        <span>搜索</span>
        <el-button color="#eb5ab6" class="search" :icon="Search" />
      </div>
      <i class="icon-qrcode"></i>
    </div>
    <!-- 标签 -->
    <!-- 在Vue 的版本里，当在组件中使用v-for时，:key是必须的。 -->
    <ul class="tab-list">
      <li
        v-for="(tab, index) in tabs"
        :key="index"
        class="tab-item"
        :class="{ 'is-active': index === activeTabIndex }"
        @click="toggleTab(tab, index)"
      >
        <el-button type="primary" class="btnTab">{{ tab }}</el-button>
      </li>
    </ul>
  </div>
  <!-- 新闻 -->
  <ul class="news-list">
    <li
      v-for="(item, index) in list"
      :key="index"
      class="news-item"
      @click="toNews(item)"
    >
      <div class="news-media">
        <img class="news-thumbnail" :src="item.thumbnail" />
        <div class="news-profile">
          <p>{{ item.title }}</p>
          <p class="news-mark">
            <Badge v-if="item.isHot" text="热点"></Badge>
            <span>{{ item.source }}</span
            >&nbsp;&nbsp;
            <span>{{ item.time }}</span>
          </p>
        </div>
      </div>
    </li>
  </ul>
  <!-- <Test :text="'activeTabIndex'" color="1"></Test> -->
</template>

<!-- 加载element-plus图标css -->
<script setup>
import { Search } from "@element-plus/icons-vue";
</script>
<script>
import { mapGetters, mapActions } from "vuex";
import Badge from "@/widgets/Badge.vue";
// import Test from '@/views/Test.vue'
export default {
  name: "about",
  components: { Badge },
  //信息存放
  data() {
    return {
      name: "about",
      visible: false,
      activeTabIndex: 0,
    };
  },
  //当页面创建，运行这个方法
  created() {
    //赋值
    this.asyncSetCategories(["推荐", "热点", "科技", "时政", "时尚", "时事"]);
    console.log(this.getCategories);
  },
  //计算属性
  computed: {
    // 分模块，只能使用这个方法访问，不分模块可直接getters取值
    ...mapGetters("Category", ["getUser"]),
    ...mapGetters("CategoryNew", ["getCategories"]),
    ...mapGetters("List", ["list"]),
    tabs() {
      // 合并数组  //导航栏
      return this.getCategories;
    },
  },
  //方法
  methods: {
    ...mapActions("CategoryNew", ["asyncSetCategories"]),
    ...mapActions("List", ["getListByCategory"]),
    //标签页切换
    toggleTab(tab, index) {
      this.activeTabIndex = index;
      this.getListByCategory({
        category: tab,
      });
    },
    //跳转
    toNews(news) {
      this.$router.push({ path: "news", query: { id: news.id } });
    },
  },
  mounted() {
    this.$nextTick(function () {
      // this.getCategories()
      this.getListByCategory({
        category: "推荐",
      });
      console.log(this.list);
    });
  },
};
</script>
